import React from 'react';
import styled from 'styled-components';
import FadeIn from 'react-fade-in';
import MainImage from '../assets/images/IMG_3790.png';
import ImageText from './ImageText';
import SeeMyProjectButton from './SeeMyProjectButton';
import ScrollDownArrow from '../assets/images/scroll-down-arrow.svg';
import LetterType from './LetterType';

const Styling = styled.div`
  .mainStyles {
    height: 100vh;
    min-height: 1000px;
    width: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  .main-heading {
    font-size: 2rem;
    margin-bottom: -9rem;
    position: relative;
    span {
      display: inline-block;
      width: 100%;
    }
    .my-name {
      font-size: 7rem;
      font-family: 'Montserrat SemiBold';
      color: white;
    }
  }
  .mainImage {
    max-width: 900px;
    width: 100%;
    height: 600px;
    margin: 0 auto;
    border: 2px solid var(--gray-1);
  }
  .my-info {
    margin-top: -18rem;
    color: white;
  }
  .scroller {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: absolute;
    bottom: 20px;
    width: 50px;
    right: 50px;
    p {
      font-size: 1.6rem;
      transform: translateY(-70px) rotate(90deg);
      letter-spacing: 0.7rem;
      text-transform: uppercase;
    }
    img {
      max-height: 70px;
      width: 16px;
      margin: 0px auto;
      object-fit: contain;
    }
  }
  @media only screen and (max-width: 768px) {
    .mainStyles {
      min-height: 795px;
    }
    .main-heading {
      font-size: 1.4rem;
      margin-bottom: -3rem;
    }
    .my-name {
      font-size: 4.5rem;
    }
    .scroller {
      display: none;
    }
    .mainImage {
      height: 300px;
    }
    .my-info {
      margin-top: 3rem;
    }
  }
`;

function MainInfo(props) {
  return (
    <Styling>
      <div className="mainStyles">
        <div className="container">
          <h1 className="main-heading">
            <span>Hello, I'm </span>
            <span className="my-name">Aruna Randika</span>
            <LetterType />
          </h1>

          <div className="mainImage">
            <img src={MainImage} alt="Aruna Randika" />
          </div>
          <div className="my-info">
            <ImageText>
              I am a Mobile Application Developer,Web Developer and an UX/UI
              designer. I consider programming is a type of poetry,we can create
              something beautiful using our creativity and words.
            </ImageText>
            <SeeMyProjectButton
              link="/projects"
              text="my projects"
              styling={false}
            />
          </div>
          <div className="scroller">
            <p>Scroll</p>
            <img src={ScrollDownArrow} alt="scroller" />
          </div>
        </div>
      </div>
    </Styling>
  );
}

export default MainInfo;
