import React from 'react';
import styled from 'styled-components';
import { MdDesktopMac, BsPhone, SiAdobeindesign } from 'react-icons/all';
import FadeIn from 'react-fade-in';
import ScrollAnimation from 'react-animate-on-scroll';
import Zoom from 'react-reveal/Zoom';
import SectionTitle from './SectionTitle';
import ServicesItem from './ServicesItem';

const Styling = styled.div`
  padding: 10rem 0;

  .services-all {
    display: flex;
    gap: 10rem;
    justify-content: space-between;
    margin-top: 5rem;
  }

  @media only screen and (max-width: 768px) {
    .services-all {
      flex-direction: column;
      max-width: 350px;
      margin: 0 auto;
      margin-top: 5rem;
      gap: 5rem;
    }
  }
`;

function ServicesComponent(props) {
  return (
    <Styling>
      <div className="container">
        <SectionTitle heading="Services" subheading="my expertise and" />
        <Zoom>
          <div className="services-all">
            <ServicesItem
              icon={<BsPhone />}
              heading="Mobile Application Development"
              description="Want to develop a beautiful,futuristic App of your own? or do you have a
        creative solution to a problem that can be handled through a Mobile App?
        I will design an develop an Application in no time."
            />
            <ServicesItem
              icon={<MdDesktopMac />}
              heading="Web Design and Development"
              description="Modern single page Web Apps with Responsive UI for your business and personal Website needs."
            />
            <ServicesItem
              icon={<SiAdobeindesign />}
              heading="UX/UI Designing"
              description="Do you want to develop a modern and outstanding UI for your Mobile/Web App?, Well you came to the right place.We do all kinds of UI/UX designing for all your needs."
            />
          </div>
        </Zoom>
      </div>
    </Styling>
  );
}

export default ServicesComponent;
