import React from 'react';
import styled from 'styled-components';

const Styling = styled.div`
  max-width: 500px;
  margin: 0 auto;
  font-size: 1.8rem;
  line-height: 1.3em;
  @media only screen and(max-width: 168px) {
    font-size: 10rem;
  }
`;

function ImageText({ children }) {
  return (
    <Styling className="para">
      <p>{children}</p>
    </Styling>
  );
}

export default ImageText;
