import React from 'react';
import Typist from 'react-typist';
import styled from 'styled-components';

const Styling = styled.div`
  text-align: center;

  .my-custom-class {
    font-family: 'RobotoMono Regular';
    font-size: 2rem;
    margin-top: 0.5rem;
    left: 50rem;
    height: 2rem;
  }
  .Cursor,
  .Cursor--blinking {
    font-size: 0rem;
  }
  @media only screen and (max-width: 768px) {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
`;

function LetterType(props) {
  return (
    <Styling>
      <Typist>
        <h1 className="my-custom-class">
          Designer
          <Typist.Backspace count={8} delay={500} />
          Developer
          <Typist.Backspace count={10} delay={500} />
          Creative Thinker
          <Typist.Backspace count={16} delay={500} />
        </h1>
      </Typist>
    </Styling>
  );
}

export default LetterType;
