import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const Styling = styled.div`
  margin-top: 2rem;
  .button {
    font-size: 2.2rem;
    background-color: ${(props) =>
      props.styling ? 'transparent' : 'var(--gray-1)'};
    padding: 0.7em 2em;
    border-radius: 8px;
    display: inline-block;
    border: 2px solid var(--gray-1);
    color: ${(props) => (props.styling ? 'var(--gray-1)' : 'black')};
  }
  @media only screen and (max-width: 768px) {
    font-size: 1.8rem;
  }
`;
function SeeMyProjectButton({
  link = 'link',
  text = 'button',
  styling = false,
}) {
  return (
    <Styling styling={styling} className="buttonWrapper">
      <Link to={link} className="button">
        {text}
      </Link>
    </Styling>
  );
}

export default SeeMyProjectButton;
