import React from 'react';
import styled from 'styled-components';
import ParticlesBg from 'particles-bg';
import SectionTitle from './SectionTitle';
import ImageText from './ImageText';
import SeeMyProjectButton from './SeeMyProjectButton';
import AboutImage from '../assets/images/IMG_3849.png';

const Stylings = styled.div`
  padding: 10rem 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    text-align: left;
  }

  .about-left,
  .about-right {
    flex: 1;
  }

  .section-title {
    text-align: left;
  }

  .para {
    margin-top: 2rem;
    margin-left: 0;
  }

  .aboutSection__buttons {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 2rem;
    margin-top: 2rem;
  }

  @media only screen and (max-width: 950px) {
    .about-left {
      flex: 4;
    }

    .about-right {
      flex: 3;
    }
  }
  @media only screen and (max-width: 768px) {
    .container {
      flex-direction: column;
      text-align: center;
    }

    .about-left,
    .about-right {
      width: 100%;
    }

    .about-right {
      margin-top: 3rem;
    }

    .section-title {
      text-align: center;
    }

    .para {
      margin: 0 auto;
      margin-top: 2rem;
    }

    .aboutSection__buttons {
      flex-direction: column;
      gap: 0rem;

      .button-wrapper,
      a {
        width: 100%;
        text-align: center;
      }
    }
  }
`;
const AboutPage = (props) => (
  <Stylings>
    <div className="container">
      <div className="about-left">
        <SectionTitle
          className="section-title"
          subheading="a brief introduction"
          heading="About Me"
        />

        <ImageText>
          I am a BSc Computer Science Undergraduate at University of Westminster
          from Polgahawela,Sri Lanka. I have been interested in Front-End
          development ever since I began programming. I love to create and
          observe artistic aspect of something.
        </ImageText>
        <div className="about-buttons">
          <SeeMyProjectButton link="/projects" text="Projects" />
        </div>
      </div>
      <div className="about-right">
        <img src={AboutImage} alt="Randika setup" />
      </div>
    </div>
  </Stylings>
);

export default AboutPage;
